.modal-movie {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%) scale(1);
  width: 280px;
  padding: 48px 20px 40px 20px;
  background-color: #ffffff;
  opacity: 1;
  
  transition: transform 250ms ease, opacity 250ms ease;

  @media screen and (min-width: 768px) {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 704px;
    padding: 40px 36px;
  }

  @media screen and (min-width: 1280px) {
    width: 806px;
    padding: 40px 12px;
}

}

.modal__close-movie-btn {
  position: absolute;
  top: 8px;
  right: 8px;
  width: 30px;
  height: 30px;
  padding: 0;
  border: 0;
  border-radius: 50%;
  background-color: transparent;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  // background-image: url(/src/images/close-icon.png);
  transition: background-color 250ms ease;

  &:hover {
    background-color: #FF6B01;
  }
}

.modal__container {
  @media screen and (min-width: 768px) {
  display: flex;
  }
}

.modal__image {
  width: 240px;
  height: 357px;
  margin-bottom: 20px;
  border-radius: 5px;
  @media screen and (min-width: 768px) {
      width: 264px;
      height: 373.55px;
      margin-right: 68px;
      margin-bottom: 0;
      }
  @media screen and (min-width: 1280px) {
      width: 375px;
      height: 478px;
      margin-right: 16px;
      }
}

.modal__info-box {

  @media screen and (min-width: 768px) {
    width: 264px;
    }
  
    @media screen and (min-width: 1280px) {
    width: 391px;
    }
}

.modal__title {
  margin-top: 0;
  margin-bottom: 20px;
  text-transform: uppercase;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.15;
  color: #000000;

  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.67;
  }
  
}

.modal__features-box {
  width: 240px;
  margin-bottom: 20px;
  @media screen and (min-width: 768px) {
    width: 264px;
    }
  @media screen and (min-width: 1280px) {
    width: 391px;
    } 
}
 
.modal__about-title, .modal__about-text {
line-height: 1.33;
text-align: left;
}

.modal__slash {
  margin-right: 3px;
  line-height: 1.33;
  color: #8C8C8C;
}

.modal__value-numbers--accent, 
.modal__value-numbers--simple {
  width: 36px;
  margin: 0;
  line-height: 1.17;
  text-align: center;
  border-radius: 5px;
}

.modal__value-numbers.modal__value-numbers--accent {
  margin-right: 3px;
  background-color: #FF6B01;
  color: #ffffff;
}

.modal__value-numbers--simple {
  background-color: #F7F7F7;
  color: #000000;
}

.modal__features-issues {
  display: flex;
  align-items: center;
  
  &:not(:last-child) {
    margin-bottom: 8px;
  }
}

.modal__values-box {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 16px;
}

.modal__keys {
  width: 108px;
  margin: 0;
  line-height: 1.33;
  color: #8C8C8C;
  flex-shrink: 0;

  @media screen and (min-width: 1280px) {
    width: 152px;
    }
}

.modal__value-text {
  // flex-grow: 1;
  margin: 0;
  line-height: 1.33;
  color: #000000;
}

.modal__value--main-text {
  text-transform: uppercase;
}

.modal__value-numbers {
  @extend .modal__value-text;
  line-height: 1.17;
}

.modal__about-title {
  margin-top: 0;
  margin-bottom: 8px;
  text-transform: uppercase;
  line-height: 1.33;
  color: #000000;
}

.modal__about-text {
  margin-top: 0;
  margin-bottom: 20px;
  line-height: 1.67;
  color: #000000;
}

.modal__bottom-thumb {
  position: relative;
}

.modal__btn-box {
  display: flex;
  justify-content: space-between;

  @media screen and (min-width: 1280px) {
  position: absolute;
  top: 0;
  left: 10px;
  }
}

.modal__btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 110px;
  height: 44px;
  padding: 0;
  line-height: 1.33;
  border: 1px solid #000000;
  border-radius: 5px;
  text-transform: uppercase;
  background-color: #ffffff;
  color: #000000;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1), 
  background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
  border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @media screen and (min-width: 768px) {
    width: 125.03px;
    
    &:not(:last-child) {
      margin-right: 15px;
    }
}

  @media screen and (min-width: 1280px) {
  width: 136px;
}

&:hover {
  background-color: #FF6B01;
  border-color: transparent;
  color: #ffffff;
}
}